body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  font-family: Roboto, serif;
}

.card__image {
  width: 160px;
  height: 134px;
  padding: 32px 19px 36px;
}

.card__info {
  padding: 0 16px 16px;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__subtitle--margin-top {
  margin-top: 4px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-subtitle {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-title {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-link {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin-top: 16px;
  padding: 12px 69px;
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__button-link:hover {
  color: #00acdc;
  background-color: #0000;
}

.stars {
  height: 16px;
  display: flex;
}

.stars__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star {
  background-image: url("star-active.edda4c43.svg");
}

.stars--4 .stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

